.main_block{
	display: flex;
	height: 85vh;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	background-color: #0057B6;
	color: white;
}

p {
    margin-block-start: 0;
    margin-block-end: 0;
}

h3 {
    margin-block-start: 0;
    margin-block-end: 0;
}

.title{
	font-size: 48px;
	width: 55%;
	text-align: left;
	margin-left: 10%;
	margin-bottom: 27px;
}

.subtitle{
	font-size: 24px;
	margin-left: 10%;
	margin-bottom: 27px;
}

.main__link{
	margin-left: 10%;
}