.header{
	display: flex;
	height: 15vh;
	align-items: center;
	justify-content: space-around;
	background-color: #0057B6;
}

.btn_header{
	height: 40px;
	margin-left: 25px;
	font-weight: bold;
	text-decoration: none;
}

.header_list{
	list-style-type: none;
	padding-inline-start: 0;
	display: inline-flex;
	align-items: center;
}

.header_list > .header_item{
	margin-right: 4rem;
}

.header_list > .header_item > .header_link{
	color: white;
	text-decoration: none;
}

.header_list > .header_item > .header_link:hover{
	color: white;
	text-decoration: none;
}

a:-webkit-any-link{
	text-decoration: none;
}